/* Общий контейнер */
/* Основной контейнер сцены */
.scene-container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }

  /* Заголовок */
  .scene-header {
    text-align: center;
    background: #282c34;
    color: white;
    padding: 10px 20px;
    font-family: "Arial", sans-serif;
  }

  .scene-header h1 {
    font-size: 1.8rem;
    margin: 0;
  }

  .scene-header p {
    font-size: 0.9rem;
    margin: 5px 0 0;
    color: #ddd;
  }

  /* Контейнер для карты */
  .scene-map {
    flex-grow: 1;
    position: relative;
    overflow: hidden;
    background: #f4f4f4;
  }

  /* SVG-карта */
  .scene-svg {
    width: 100%; /* Растягиваем SVG на ширину экрана */
    height: auto; /* Поддерживаем пропорции */
    touch-action: none; /* Отключаем стандартные жесты браузера для удобства панорамирования */
  }

  /* Заголовок */
  .scene-header {
    text-align: center;
    margin-bottom: 20px;
  }

  .scene-header h1 {
    font-size: 2rem;
    color: #333;
    margin: 0;
  }

  .scene-header p {
    font-size: 1rem;
    color: #666;
    margin: 5px 0 0;
  }

  /* Карта */
  .scene-map {
    border: 2px solid #ddd;
    border-radius: 10px;
    overflow: hidden;
    background-color: #fff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }

  /* SVG-карта */
  .scene-svg {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  /* Места */
  .seat {
    cursor: pointer;
    transition: fill 0.3s ease, transform 0.3s ease;
  }

  .seat.occupied {
    fill: gray !important;
    cursor: not-allowed;
  }

  .seat.free:hover {
    transform: scale(1.2);
  }

  /* Адаптивность */
  @media (max-width: 768px) {
    .scene-header h1 {
      font-size: 1.5rem;
    }

    .scene-header p {
      font-size: 0.9rem;
    }

    .scene-map {
      height: 60vh;
    }
  }

  @media (max-width: 480px) {
    .scene-map {
      height: 50vh;
      border: 1px solid #ccc;
    }
  }


.seat {
