body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.seat {
  cursor: pointer;
}

.seat.free path {
  fill: black; /* Свободные места */
}

.seat.occupied path {
  fill: red; /* Занятые места */
}


.seat.occupied1 path {
  fill: red; /* Занятые места */
}
.seat.occupied2 path {
  fill: yellow; /* Занятые места */
}
.seat.occupied3 path {
  fill: green; /* Занятые места */
}
.seat.occupied4 path {
  fill: orange; /* Занятые места */
}
.seat.occupied5 path {
  fill: blue; /* Занятые места */
}